				let foods=[];
				this.goods.forEach((good)=>{
					good.foods.forEach((food)=>{
						if(food.count){
							foods.push(food);
						}
					});
				});
				return foods ;
			}
		},
		methods:{
		 selectMenu(key,event){
		 	if(!event._constructed){
		 		return;
		 	}
		  	let foodlist=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
		 	let el=foodlist[key];
		 	this.foodsScroll.scrollToElement(el,300);
		 	//console.log(key);
		 	
		 	
		 },
		 _initScroll(){
		 	//console.log(BScroll);
		 	this.meunScroll = new BScroll(this.$refs.menuWrapper,{
		 		click:true
		 	});
		 	this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
		 		probeType:3,
		 		click:true
		 		
		 	});
		 	 this.foodsScroll.on('scroll',(pos)=>{
		 	 	this.scrollY=Math.abs(Math.round(pos.y));
		 	 	//console.log(this.scrollY);
		 	 });
		  },
		  _calculateHeight(){
		  	let foodlist=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
		  	let height=0;
		  	   this.listHeight.push(height);
		  	for (let i=0;i<foodlist.length;i++){
		  		let item=foodlist[i];
		  		height+=item.clientHeight;
		  		this.listHeight.push(height);
		  	}
		  },
		  selectFood(food,event) {
		  	 if(!event._constructed){
		  	 	return ;
		  	 }else{
		  	 	this.selectedFood=food;
		  	 	this.$refs.food.show();
		  	 }
		  }
		},
		components:{
		'shopcart':shopcart,
		 'cartcontral':cartcontral,
		 'food':food
		}
	
	}
	
	
</script>
<style lang="stylus" rel="stylesheet/stylus"> 
.goods
 display:flex;
 position:absolute;
 top:174px;
 bottom:46px;
 width:100%;
 overflow:hidden;
 .menu-wrapper
  flex:0,0,80px;
  width:80px;
  background:#f3f5f7;
  .menu-item
   display:table;
   height:54px;
   width:56px;
   padding:0 12px;
   line-height:14px;
   &.current
     position:relative;
     z-index:10;
     margin-top:-1px;
     background: #fff;
     font-weight:700;
     .text
      border:none;
   .icon
    display:inline-block;
    vertical-align:top;
    width:12px;
    height:12px;
    margin-right:2px;
    background-size:12px 12px;
    background-repeat:no-repeat;
    &.special
     background-image:url('special_3@2x.png');
    &.decrease
      background-image:url('decrease_3@2x.png');
    &.discount
      background-image:url('discount_3@2x.png');
	&.invoice
	  background-image:url('invoice_3@2x.png');    
	&.guarantee
	  background-image:url('guarantee_3@2x.png');
	.text
      font-size:12px;
      line-height:14px;
      border-bottom:1px solid rgba(7,17,27,0.1);
      vertical-align:middle;
      display:table-cell;
      width:56px;
 .foods-wrapper
  text-align: left;
  flex:1;
  .title
   padding-left:14px;
   height:26px;
   line-height:26px;
   border-left:2px solid #d9dde1;
   font-size:12px;
   color:rgb(147,153,169);
   background: #f3f5f7;
  .food-item
   display:flex;
   margin:18px;
   padding-bottom:18px
   border-bottom:1px solid rgba(7,17,27,0.1);
   &:last-child
    border:none;
    margin-bottom:0;
   .icon
    flex:0,0,57px;
    img
     width:57px;
     height:57px;
     margin-right:10px;
   .content
     flex:1;
     .name
      margin:2px 0 8px 0;
      line-height:14px;
      height:14px;
      font-size:14px;
      color:rgb(7,17,27);
     .desc,.extra  
      line-height:10px;
      font-size:10px;
      color:rgb(147,153,159);
     .desc
      margin-bottom:8px;
      line-height:12px;
     .extra
      .count
        margin-right:12px;
     .price
      display:inline-block;
      font-weight:700;
      line-height:24px;
      .now
       font-size:8px;
       margin-right:8px;
       color:rgb(240,20,20);
      .old
       text-decoration: line-through;
       font-size:10px;
       color:rgb(147,153,159);
     .contral-wrapper
      display:inline-block;
      float:right;
      right:0px;
      bottom:0px;
</style>


